import React from 'react'
import './SignIn.scss'

type SignInTypes = {
    setSignIn: (prev: boolean) => void
    signin: boolean
}
export const SignIn = ({setSignIn, signin}: SignInTypes) => {
    return (
        <div className='SignIn'>
            <form className="SignIn__form" onSubmit={() => setSignIn(!signin)}>
                <input placeholder='Введите ваше имя' type="text" name="" id=""/>
                <input placeholder='Введите ваш e-mail ' type="text" name="" id=""/>
                <button type="submit">Войти</button>
            </form>
            <div className="SignIn__youtube">
                <span className="SignIn__youtube-span">Войти с помощью</span>
                <div className="SignIn__youtube-btn" onClick={() => setSignIn(!signin)}>YouTube</div>
            </div>
        </div>
    )
}
