import React from 'react'
import './Statistics.scss'

import moneySvg from '../../assets/image/money.svg'

export const Statistics = () => {
    return (
        <div className='Statistics'>
            <div className="Statistics__inner">
                <div className="Statistics__views">
                    <img src={moneySvg} alt="" className="Statistics__icon"/>
                    <div className="Statistics__info">
                        <div className="Statistics__count">172 413</div>
                        <div className="Statistics__text">просмотров за месяц</div>
                    </div>
                </div>
                <div className="Statistics__month">
                    <img src={moneySvg} alt="" className="Statistics__icon"/>
                    <div className="Statistics__info">
                        <div className="Statistics__count">83 900 P</div>
                        <div className="Statistics__text">за месяц</div>
                    </div>
                </div>
            </div>
        </div>
    )
}
