import React, { useState } from 'react'
import { LineChart, SignIn, Statistics } from '../../components'
import { BarChart } from '../../components/Chart/BarChart/BarChart'


import './Main.scss'
export const Main = () => {
    const [signin, setSignIn] = useState(true)

    if (signin) {
        return <SignIn signin setSignIn={setSignIn} />
    }

    return (
        <div className='Main'>
            <Statistics />
            <div className="Main__charts">
                <LineChart />
                <BarChart />
            </div>
        </div>
    )
}
