import React, { useState } from 'react'
import { Line } from 'react-chartjs-2'




import './LineChart.scss'
const data = {
    labels: ['2.02.21', '10.02.21', '18.02.21', '26.02.21'],
    datasets: [{
        data: [967, 10008, 5698, 12345, 2, 3],
        borderColor: [
            'rgba(235, 255, 111, 1)',
            'rgba(235, 255, 111, 1)',
            'rgba(235, 255, 111, 1)',
            'rgba(235, 255, 111, 1)',
            'rgba(235, 255, 111, 1)',
        ],
        borderWidth: 3,
        lineTension: 0,
        pointBackgroundColor: 'black'
    }]
}

const options = {
    maintainAspectRatio : false,
    scales: {
        yAxes: [{
            ticks: {
                fontSize: 0 
            }
        }],
        xAxes: [{
            ticks: {
                fontSize: 16,
                FontFamily: 'BellotaText',
            }
        }]
    },
    legend: {
        display: false
    },
    elements: {
        point: {
            radius: 10,
            hoverRadius: 10,
            hoverBorderWidth: 3
        }
    },
    tooltips: {
        mode: 'index',
        titleFontSize: 0,
        bodyFontFamily: 'BellotaText',
        bodyFontSize: 22,
        fontWeight: 700,
        bodyFontColor: '#424242',
        displayColors: false,
        backgroundColor: 'white'
    }
}
export const LineChart = () => {
    const [openMonthFilter, setOpenMonthFilter] = useState(false)
    const [selectMonth, setSelectMonth] = useState('Февраль')
    const month = ['Январь', 'Февраль', 'Март', 'Апрель', 'Май', 'Июнь', 'Июль', 'Август', 'Сентябрь', 'Октябрь', 'Ноябрь', 'Декабрь']
   
    const selectHandlerMonth = (index: number) => {
        setSelectMonth(month[index])
    }

    return (
        <div className='LineChart'>
            <div className="LineChart__inner">
                <div className="LineChart__top">
                    <div className="LineChart__top-name">Название графика</div>
                    <div className="LineChart__top-month" onClick={() => setOpenMonthFilter(prev => !prev)}>
                    {selectMonth}
                        {openMonthFilter && <ul>
                            {month.map((item, index) => (
                                <li 
                                key={item + index}
                                onClick={() => selectHandlerMonth(index)}
                                >{item}</li>
                            ))}
                        </ul>}
                    </div>
                </div>
                <div className="LineChart__main">
                    <Line data={data} height={100} options={options} />
                </div>
            </div>
        </div>
    )
}
