import React from 'react'

import './Account.scss'

export const Account = () => {
    return (
        <div className="Account">
            <div className="Account__inner">
                <form action="" className="Account__form">
                    <label htmlFor="youtubeId">YouTube ID
                        <input id="youtubeId" placeholder="YouTube ID" type="text"/>
                    </label>
                    <label htmlFor="name">Имя
                        <input id="name" placeholder="Имя" type="text"/>
                    </label>
                    <label htmlFor="email">E-mail
                        <input id="email" placeholder="E-mail" type="text"/>
                    </label>
                    <label htmlFor="number">Телефон
                        <input id="number" placeholder="+7(XXX)XXX-XX-XX" type="text"/>
                    </label>
                    <button type="submit">Сохранить</button>
                </form>
            </div>
        </div>
    )
}
